@import "../../../../style/variables.scss";

.main-modal {
    .wrapper {
        margin-top: 8px;
    }
    &-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        ol {
            li {
                font-size: 18px;
            }
        }
        input {
            width: 300px;
        }
    }

    button {
        margin-right: 20px;
        padding: 2px 6px;
        line-height: 16px;
        color: $dark200;
        background: $light100;
        box-shadow: inset 0px 0px 1px 1px rgba(0, 0, 0, 0.25);
        border-radius: 4px;
    }
    .error {
        color: red;
    }
}
